body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: flex;
  width: 100%; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

main {
  flex-grow: 1;
  padding: 0; }

.home {
  position: relative;
  background-attachment: fixed;
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  width: 100%; }
  .home__screen {
    position: relative; }
  .home__wrapper-1 {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #f3eded88;
    padding: 2rem; }
  .home__title {
    font-size: 6rem;
    color: #690069;
    font-weight: 700;
    text-transform: uppercase;
    text-align: left;
    line-height: 1.2;
    margin: 0;
    padding: 0; }
  .home__description {
    font-size: 2rem;
    color: #690069;
    font-weight: 400;
    text-transform: uppercase;
    text-align: left;
    line-height: 1.2;
    margin: 0;
    padding: 0; }

.event-data {
  background: #f3eded88;
  padding: 1rem;
  box-sizing: border-box;
  margin: 2rem 1rem;
  color: #690069; }
  .event-data__title {
    font-size: 1.4rem;
    color: #690069;
    font-weight: 700;
    text-transform: uppercase;
    text-align: left;
    line-height: 1.2;
    margin: 0 0 1rem 0;
    padding: 0; }
  .event-data__row {
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start; }
  .event-data__row-title {
    font-weight: 600;
    margin-right: 2rem;
    flex: 0 0 1; }

.root {
  overflow: hidden; }
  .root .sticky, .root .sticky2 {
    height: 100vh;
    width: 100%; }
  .root .heading {
    position: absolute;
    height: 100%;
    width: 100%; }

.calendar__events-list {
  list-style: none;
  margin: 0;
  padding: 0; }

.calendar__events-list-item {
  font-size: .8rem;
  line-height: 1;
  display: block; }

.todo-list {
  position: relative; }
  .todo-list__add-new-btn {
    position: fixed !important;
    right: 2rem;
    bottom: 2rem;
    z-index: 1; }

.add-todo-form {
  display: flex;
  flex-direction: column;
  padding: 4rem; }

.guests-list {
  list-style: none;
  text-align: left;
  padding: 0; }

.guest {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: stretch;
  align-items: center; }
  .guest__data {
    width: 50%;
    flex-grow: 1;
    padding: 0 2rem; }
  .guest__contacts {
    width: 25%; }
  .guest__checkin {
    width: 10%;
    text-align: center; }

.event-plan__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap; }

.inner-list {
  display: flex;
  flex-direction: column;
  max-height: 100vh;
  overflow: auto;
  padding: 0;
  max-width: 25vw; }
  .inner-list .inner-list__header {
    background: #ffffff; }
  .inner-list .inner-list__item {
    display: block; }

.detailsDrawer__content {
  width: 240px;
  padding: 40px 20px; }

.sideDrawer {
  overflow: hidden; }
  .sideDrawer__header {
    text-align: end; }
  .sideDrawer_open {
    width: 240px; }
  .sideDrawer_close {
    width: 58px; }

.menu-item__link {
  display: flex;
  justify-content: flex-start;
  align-items: center; }

.menu-item__icon {
  width: 1.4rem;
  font-size: 1.4rem; }

